.primary-header{
    position: relative;
    height: 60vh;
    min-height: 500px;
    background-color: var(--primary-color);
}
.primary-header>.container{
    position: relative;
    z-index: 3;
}
/* Slider Container */
.slider {
    margin: auto;
    overflow: hidden;
    z-index: 1;
  }

  /* Slides Wrapper */
  .slides {
    display: flex;
    transition: transform 1s ease-in-out;
  }

  /* Individual Slide */
  .slide {
    min-width: 100%;
    box-sizing: border-box;
    position: relative;
    &::after{
        content: "";
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: var(--black-color);
        position: absolute;
        z-index: 1;
        opacity: .2;

    }
    & >p{
        z-index: 2;
        background: #00000063;
        padding: 8px 15px;
        border-radius: 5px;
        font-size: 14px;
        opacity: 0;
        transition-delay: 0s;
    }
  }
  .slide.slider-active>p{
    opacity: 1;
    transition-delay: 1.5s;
}
.site-logo{
    aspect-ratio: 1/1;
    width: clamp(70px, 6.25vw, 120px);
    background-color: var(--white-color);
}
.llisting >a{
    position: relative;
    z-index: 2;
}
